*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: ubuntu;
}

.heading{
  color: red;
}

body{
  text-align: center;
  background: linear-gradient(to right,  #11998e, #38ef7d);
  color: #e5e5e5;
}

header{
  min-height: 8vh;
  background: #e5e5e5;
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-transform: uppercase;
  color: rgba(0, 0, 0, 0.6);
  margin: 0 0 7px 0;
}

main{
  float: left;
  text-align: center;
  margin: 200px 0px 0px 250px;
}

header nav ul {
  list-style: none;
}

svg{
  height: 400px;
  float: right;
}

#right-wheel, #left-wheel{
  animation: wheel 1.5s ease infinite alternate;
  transform-origin: center;
  transform-box: fill-box;
}

@keyframes wheel{
  from{
    transform: rotateZ(0deg);
  }
  to{
    transform: rotateZ(360deg);
  }
}

svg#hand_svg{
  float: left;
  margin: 3px 0px 3px 120px;
}

#hand{
  animation: wave 1s ease-in-out infinite alternate;
  transform-origin: center;
  tranform-box: fill-box;

}

@keyframes wave{
  from{
    transform: rotateZ(-deg);
  }
  to{
    transform: rotateZ(17deg);
  }
}

aside{
  float: right;
  text-align: center;
  margin: 150px 350px 0px 0px;
}
  
footer{
  margin-top: 850px;
  min-height: 8vh;
  text-align: center;
  background: #e5e5e5;
  color: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
}

@media only screen and (max-width: 700px){
  main{
    float: none;
    margin: 100px 0 50px 0;
  }

  svg{
    float: none;
    margin: -280px;
    height: 250px;
  }

  svg#hand_svg{
    float: none;
    height: 185px;
    margin: 344px 12px 3px -85px;
  }

  aside{
    float: none;
    text-align: center;
    margin: 80px 0px 0px 0px;
  }
  footer{
    margin-top: 50px;
  }
}